import React from "react";
// import Unity from "react-unity-webgl"
import { Unity, useUnityContext } from "react-unity-webgl";

function App() {
  const { unityProvider } = new useUnityContext({
    dataUrl: "build/ReactBuild.loader.js",
    frameworkUrl: "build/ReactBuild.framework.js",
    loaderUrl: "build/ReactBuild.loader",
    codeUrl: "build/ReactBuild.wasm",
  });
  return <Unity unityProvider={unityProvider} style={{ width: 800, height: 600, border: "solid black 1px" }} />;
}

export default App
